

























































import { Component, Vue, Watch } from 'vue-property-decorator';
import Konva from 'konva';

enum Menu {
    Close = 'close',
    Copy = 'copy',
    Save = 'save'
}

enum Brush {
    RectFill = 'rectFill',
    Rect = 'rect',
    Ellipse = 'ellipse',
    Pencil = 'pencil',
    Text = 'text',
    Line = 'line',
    Arrow = 'arrow'
}

enum ExceptedError {
    DrawReady = 'ready'
}

interface ICanvas {
    stage: any,
    layer: any,
    draw: any[],             // 绘制的图形数组
    graphNow: any,           // 当前图形
    flag: string,            // 激活绘制-铅笔 pencil:铅笔 ellipse:椭圆 rect:矩形 rectH:矩形-空心
    drawing: boolean,        // 绘制中
    graphColor: string,      // 默认颜色
    pointStart: any[]        // 初始坐标
}

interface IOption {
    specColor: string[],
    specSize: string[]
}

interface IDrawOption {
    x?: number;                    // 画布开始坐标x
    y?: number;                    // 画布开始坐标y
    width?: number;                // 画布宽度
    height?: number;               // 画布高度
    baseZIndex?: number;           // 纵向堆叠深度（默认2000）
    menus?: Menu[];                // 需要显示的菜单，默认全部
    // drawMode?: DrawMode;     // 绘图模式
    elementSelector?: string;      // 画布容器选择器 
    exitByKeyEsc?: boolean;        // 是否允许键盘按键Esc退出

    // 工作台加载完成后的回调函数（ws: wrokspace）
    onWorkspaceReady?: (wsX: number, wsY: number, wsWidth: number, wsHeight: number, baseZIndex: number) => void;

    // 退出工作台之前的回调函数，如果返回false，将阻止退出
    beforeWorkspaceExit?: () => boolean | Promise<boolean> | void;

    // 退出工作台之后的回调函数
    afterWorkspaceExit?: () => void;

    // 复制之前的回调函数，如果返回false，将阻止复制
    beforeCopy?: (canvas: HTMLCanvasElement, close: Function) => boolean | Promise<boolean> | void;

    // 复制之后的回调函数，如果返回false，将阻止工作台退出 [imageData: base64格式的图片]
    afterCopy?: (imageData: string) => boolean | void;

    // 保存之前的回调函数，如果返回false，将阻止保存
    beforeSave?: (canvas: HTMLCanvasElement, close: Function) => boolean | Promise<boolean> | void;

    // 保存之后的回调函数，如果返回false，将阻止工作台退出 [imageData: base64格式的图片]
    afterSave?: (imageData: string) => boolean | void;
}

interface IDrawResult {
    message: string;
    close: () => void;
    save: () => void;
    copy: () => void;
}

@Component({ name: 'Sketchpad' })
export default class Sketchpad extends Vue {

    private visible: boolean = false;
    private workspaceId: string = 'sketchpad_workspace';
    private brushWrapperId: string = 'brush_wrapper'; 
    private brushBoxBottom: string = '-100%';
    private drawOption: IDrawOption = {};
    private activeBrush: string = 'pencil';
    private activeSpecColor: string = '#F56C6C';
    private activeSpecSize: string = '6px';
    private currentDocWinWidth: number;
    private currentDocWinHeight: number;

    private brushCanvas: ICanvas = {
        stage: null,
        layer: null,
        draw: [],
        graphNow: null,
        flag: null,
        drawing: false,
        graphColor: null,
        pointStart: []
    };

    private readonly defaultZIndex: number = 1000;
    private readonly drawReady: ExceptedError = ExceptedError.DrawReady;

    private readonly option: IOption = {
        specColor: ['#F56C6C', '#E6A23C', '#67C23A', '#303133'],
        specSize: ['6px', '9px', '12px', '15px']
    };

    private readonly defaultDrawOption: IDrawOption = {
        x: 0,
        y: 0,
        width: document.documentElement.clientWidth,
        height: document.documentElement.clientHeight,
        baseZIndex: 2000,
        menus: [Menu.Close, Menu.Save, Menu.Copy],
        elementSelector: this.brushWrapperId,
        exitByKeyEsc: false
    };
    

    @Watch('activeBrush', { immediate: true })
    onActiveBrushChanged(val: string, oldVal: string): void {
        let f: string = 'pencil';
        switch (val) {
            case Brush.RectFill: f = 'rect'; break;
            case Brush.Rect: f = 'rectH'; break;
            case Brush.Ellipse: f = 'ellipse'; break;
            // case Brush.Line: f = 'pencil'; break;
            // case Brush.Arrow: f = 'pencil'; break; 
            case Brush.Pencil: f = 'pencil'; break;
            case Brush.Text: f = 'text'; break;
            default: f = 'pencil'; break;
        }
        this.brushCanvas.flag = f;
    }

    @Watch('activeSpecColor', { immediate: true })
    onActiveSpecColorChanged(val: string, oldVal: string): void {
        this.brushCanvas.graphColor = val;
    }

    startDraw(customDrawOption: IDrawOption) {
        return new Promise((resolve, reject) => {
            this.registerEventListener();


            this.currentDocWinWidth = document.documentElement.clientWidth;
            this.currentDocWinHeight = document.documentElement.clientHeight;

            this.calculateOption(customDrawOption).then(drawOption => {
                return this.prepareBrushStage(drawOption.elementSelector, drawOption.width, drawOption.height);
                
            }).then(brushWrapper => {                    
                return this.prepareBrushToolbox(brushWrapper);

            }).then(data => {
                throw new Error(this.drawReady);

            }).catch(err => {
                const drawResult: IDrawResult = {
                    message: err.message,
                    close: () => { this.exit(); },
                    save: () => { this.save(); },
                    copy: () => { this.copy(); }
                };

                switch (err.message) {
                    case ExceptedError.DrawReady:
                        resolve(drawResult);
                        break;

                    default: reject(err); break;
                }
            });
        });
    }

    async calculateOption(customDrawOption: IDrawOption): Promise<IDrawOption> {
        let currentOption = Object.assign({}, this.defaultDrawOption, customDrawOption || {});
        currentOption.width = customDrawOption.width || this.currentDocWinWidth;
        currentOption.height = customDrawOption.height || this.currentDocWinHeight;
        currentOption.elementSelector = currentOption.elementSelector || this.brushWrapperId;
        this.drawOption = currentOption;
        return this.drawOption;
    }

    async prepareBrushStage(container: string, width: number, height: number): Promise<HTMLElement> {
        let brushWrapper = document.querySelector(`#${container}`) as HTMLElement;
        if (!brushWrapper) throw new Error('the brush wrapper can not be found.');

        this.visible = true;
        
        brushWrapper.innerHTML = '';

        // 1 create stage
        this.brushCanvas.stage = new Konva.Stage({ container: container, width: width, height: height });

        // 2 create layer
        this.brushCanvas.layer = new Konva.Layer();
        this.brushCanvas.stage.add(this.brushCanvas.layer);

        // 3 create our shape
        // 移除改变大小事件
        this.brushCanvas.stage.on('mousedown', e => {
            // 如果点击空白处 移除图形选择框
            // console.log(e);

            if (e.target === this.brushCanvas.stage) {
                this.stageMousedown(this.brushCanvas.flag, e);

                // 移除图形选择框
                this.brushCanvas.stage.find('Transformer').destroy();
                this.brushCanvas.layer.draw();
                return;
            }

            // 如果没有匹配到就终止往下执行
            if (!e.target.hasName('line') 
                && !e.target.hasName('ellipse') 
                && !e.target.hasName('rect') 
                && !e.target.hasName('circle')) {
                return;
            }

            // 移除图形选择框
            this.brushCanvas.stage.find('Transformer').destroy();

            // 当前点击的对象赋值给graphNow
            this.brushCanvas.graphNow = e.target;
            // 创建图形选框事件
            const tr = new Konva.Transformer({
                borderStroke: '#000', // 虚线颜色
                borderStrokeWidth: 1, //虚线大小
                borderDash: [5], // 虚线间距
                keepRatio: false // 不等比缩放
            });
            this.brushCanvas.layer.add(tr);
            tr.attachTo(e.target);
            this.brushCanvas.layer.draw();
        });

        // 鼠标移动
        this.brushCanvas.stage.on('mousemove', e => {
            if (this.brushCanvas.graphNow && this.brushCanvas.flag && this.brushCanvas.drawing) {
                this.stageMousemove(this.brushCanvas.flag, e);
            }
        });

        // 鼠标放开
        this.brushCanvas.stage.on('mouseup', () => {
            this.brushCanvas.drawing = false;
            if (this.brushCanvas.flag === 'text') this.brushCanvas.flag = null;
        });

        return brushWrapper;
    }

    prepareBrushToolbox(brushWrapper: HTMLElement): Promise<any> {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                this.brushBoxBottom = '0';
                brushWrapper.style.backgroundColor = 'rgba(0, 0, 0, 0.1)';
                
                resolve(null);
            }, 50);
        });
    }

    stageMousedown(flag, ev) {
        if (flag) {
            let x = ev.evt.offsetX, y = ev.evt.offsetY;
            this.brushCanvas.pointStart = [x, y];

            switch (flag) {
                case 'pencil': this.drawPencil(this.brushCanvas.pointStart, this.brushCanvas.graphColor, 2); break;
                case 'ellipse': this.drawEllipse(x, y, 0, 0, this.brushCanvas.graphColor, 2); break;
                case 'rect': this.drawRect(x, y, 0, 0, this.brushCanvas.graphColor, 0); break;
                case 'rectH': this.drawRect(x, y, 0, 0, this.brushCanvas.graphColor, 2); break;
                case 'text': this.drawText(x, y, this.brushCanvas.graphColor, 16); break;
                default: break;
            }
            this.brushCanvas.drawing=true;
        }
    }

    stageMousemove(flag, ev) {
        switch (flag) {
            case 'pencil':
                // 铅笔
                this.brushCanvas.pointStart.push(ev.evt.offsetX, ev.evt.offsetY);
                this.brushCanvas.graphNow.setAttrs({
                    points: this.brushCanvas.pointStart
                });
                break;
            case 'ellipse':
                // 椭圆
                this.brushCanvas.graphNow.setAttrs({
                    radiusX: Math.abs(ev.evt.offsetX - this.brushCanvas.pointStart[0]),
                    radiusY: Math.abs(ev.evt.offsetY - this.brushCanvas.pointStart[1])
                });
                break;
            case 'rect':
            case 'rectH':
                this.brushCanvas.graphNow.setAttrs({
                    width: ev.evt.offsetX - this.brushCanvas.pointStart[0],
                    height: ev.evt.offsetY - this.brushCanvas.pointStart[1]
                });
                break;
            default:
                break;
        }
        this.brushCanvas.layer.draw();
    }

    drawPencil(points, stroke, strokeWidth) {
        const line = new Konva.Line({
            name: 'line',
            points: points,
            stroke: stroke,
            strokeWidth: strokeWidth,
            lineCap: 'round',
            lineJoin: 'round',
            tension: 0.5,
            draggable: true
        });
        this.brushCanvas.graphNow = line;
        this.brushCanvas.layer.add(line);
        this.brushCanvas.layer.draw();

        line.on('mouseenter', () => {
            this.brushCanvas.stage.container().style.cursor = 'move';
        });

        line.on('mouseleave', () => {
            this.brushCanvas.stage.container().style.cursor = 'default';
        });

        line.on('dblclick', () => {
            // 双击删除自己
            // this.remove();
            line.remove();
            this.brushCanvas.stage.find('Transformer').destroy();
            this.brushCanvas.layer.draw();
        });
    }

    drawEllipse(x, y, rx, ry, stroke, strokeWidth) {
        const ellipse = new Konva.Ellipse({
            name: 'ellipse',
            x: x,
            y: y,
            radiusX: rx,
            radiusY: ry,
            stroke: stroke,
            strokeWidth: strokeWidth,
            draggable: true
        });
        this.brushCanvas.graphNow = ellipse;
        this.brushCanvas.layer.add(ellipse);
        this.brushCanvas.layer.draw();

        ellipse.on('mouseenter', () => {
            this.brushCanvas.stage.container().style.cursor = 'move';
        });

        ellipse.on('mouseleave', () => {
            this.brushCanvas.stage.container().style.cursor = 'default';
        });

        ellipse.on('dblclick', () => {
            // 双击删除自己
            // this.remove();
            ellipse.remove();
            this.brushCanvas.stage.find('Transformer').destroy();
            this.brushCanvas.layer.draw();
        });
    }

    drawRect(x, y, w, h, c, sw) {
        const rect = new Konva.Rect({
            name: 'rect',
            x: x,
            y: y,
            width: w,
            height: h,
            fill: sw===0?c:null,
            stroke: sw>0?c:null,
            strokeWidth: sw,
            opacity: sw===0?0.5:1,
            draggable: true
        });
        this.brushCanvas.graphNow=rect;
        this.brushCanvas.layer.add(rect);
        this.brushCanvas.layer.draw();

        rect.on('mouseenter', () => {
            this.brushCanvas.stage.container().style.cursor = 'move';
        });

        rect.on('mouseleave', () => {
            this.brushCanvas.stage.container().style.cursor = 'default';
        });

        rect.on('dblclick', () => {
            // 双击删除自己
            // this.remove();
            rect.remove();
            this.brushCanvas.stage.find('Transformer').destroy();
            this.brushCanvas.layer.draw();
        });
    }

    drawText(x, y, fill, fs) {
        let text = new Konva.Text({
            text: '双击编辑文字',
            x: x,
            y: y,
            fill: fill,
            fontSize: fs,
            width: 300,
            draggable: true
        });
        this.brushCanvas.graphNow = text;
        this.brushCanvas.layer.add(text);
        this.brushCanvas.layer.draw();

        text.on('mouseenter', () => {
            this.brushCanvas.stage.container().style.cursor = 'move';
        });

        text.on('mouseleave', () => {
            this.brushCanvas.stage.container().style.cursor = 'default';
        });

        text.on('dblclick', () => {
            // 在画布上创建具有绝对位置的textarea

            // 首先，我们需要为textarea找到位置

            // 首先，让我们找到文本节点相对于舞台的位置:
            let textPosition = text.getAbsolutePosition(); // f

            // 然后让我们在页面上找到stage容器的位置
            let stageBox = this.brushCanvas.stage.container().getBoundingClientRect();

            // 因此textarea的位置将是上面位置的和
            let areaPosition = {
                x: stageBox.left + textPosition.x,
                y: stageBox.top + textPosition.y
            };

            // 创建textarea并设置它的样式
            let textarea = document.createElement('textarea');
            let ctn = document.querySelector('#brush_wrapper .konvajs-content');
            if (ctn) ctn.appendChild(textarea);
            // document.body.appendChild(textarea);

            let T = text.text();  //f
            if (T === '双击编辑文字') {
                textarea.value = '';
                textarea.setAttribute('placeholder','请输入文字')
            } else {
                textarea.value = T;
            }

            textarea.style.position = 'absolute';
            textarea.style.zIndex = '10';
            textarea.style.top = (areaPosition.y - this.drawOption.y) + 'px';
            textarea.style.left = (areaPosition.x- this.drawOption.x) + 'px';
            textarea.style.background = 'none';
            textarea.style.border = '1px dashed #000';
            textarea.style.outline = 'none';
            textarea.style.color = text.fill();   //f
            textarea.style.width = text.width().toString();  //f

            textarea.focus();

            text.setAttr('text', '');  //f
            this.brushCanvas.layer.draw();

            // 确定输入的文字
            let confirm = (val) => {
                text.text(val?val:'双击编辑文字');  //f
                this.brushCanvas.layer.draw();
                // 隐藏在输入
                if (textarea) {
                    // document.body.removeChild(textarea);
                    ctn.removeChild(textarea);
                }
            };
            // 回车键
            let keydown = (e) => {
                if (e.keyCode === 13) {
                    textarea.removeEventListener('blur', blur);
                    confirm(textarea.value)
                }
            };
            // 鼠标失去焦点
            let blur = () => {
                textarea.removeEventListener('keydown', keydown);
                confirm(textarea.value);
            };

            textarea.addEventListener('keydown', keydown);
            textarea.addEventListener('blur', blur);
        });
    }



    mergeCanvas(from, to) {
        return new Promise((resolve, reject) => {
            if (!from || !to) reject();


            let sourceImageData = from.toDataURL('image/png');
            let destCanvasContext = to.getContext('2d');

            let destImage = new Image;
            destImage.onload = () => {
                destCanvasContext.drawImage(destImage, 0, 0, from.width, from.height, 0, 0, to.width, to.height);
                resolve(null);
            };
            destImage.src = sourceImageData;
        });
    }

    async exit(keyEsc?: boolean): Promise<boolean> {
        if (keyEsc === true && !this.drawOption.exitByKeyEsc) throw new Error('esc is prevent.');

        if (typeof(this.drawOption.beforeWorkspaceExit) === 'function') {
            let bweRst = await this.drawOption.beforeWorkspaceExit();
            if (bweRst === false) throw new Error('exit is prevent.');
        }

        let workspace = document.querySelector(`#${this.workspaceId}`) as HTMLElement;
        if (!workspace) throw new Error('the element[id=workspace] is not exist.');

        let brushWrapper = workspace.querySelector(`#${this.brushWrapperId}`) as HTMLElement;
        if (!brushWrapper) throw new Error('the element[id=brush_wrapper] is not exist.');

        workspace.style.backgroundColor = '';
        brushWrapper.style.backgroundColor = '';
        brushWrapper.innerHTML = '';
        this.brushBoxBottom = '-100%';

        setTimeout(() => {
            this.visible = false;
            
            if (typeof(this.drawOption.afterWorkspaceExit) === 'function') {
                this.drawOption.afterWorkspaceExit();
            }
        }, 300);
        
        this.unregisterEventListener();

        return true;
    }

    save_old() {
        let bgCvs = document.querySelector('#${this.canvasId}') as HTMLCanvasElement;
        if (!bgCvs) return;

        let brushCvs = document.querySelector('#brush_wrapper canvas') as HTMLCanvasElement;
        this.mergeCanvas(brushCvs, bgCvs).then(() => {
            let link = document.createElement('a');
            // let imgData = bgCvs.toDataURL({ format: 'png', quality: 1, width: canvas.width, height: canvas.height });
            let imgData = bgCvs.toDataURL('png', 1);                
            // let strDataURI = imgData.substr(22, imgData.length);

            let arr = imgData.split(','), mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
            while(n--) { u8arr[n] = bstr.charCodeAt(n); }
            let blobData = new Blob([u8arr], { type: mime });

            link.download = '屏幕截图' + new Date().getTime() + '.png';
            link.href = URL.createObjectURL(blobData);
            link.click();
            link.remove();
            setTimeout(() => { this.exit(); }, 200);
        });
    }

    copy_old() {
        let bgCvs = document.querySelector('#${this.canvasId}') as HTMLCanvasElement;
        if (!bgCvs) return;

        let brushCvs = document.querySelector('#brush_wrapper canvas') as HTMLCanvasElement;
        this.mergeCanvas(brushCvs, bgCvs).then(() => {
            let img = document.createElement('img');
            img.src = bgCvs.toDataURL('image/png');
            document.body.appendChild(img);

            if (window.getSelection) {
                let selection = window.getSelection();
                selection.removeAllRanges();
                let range = document.createRange();
                range.selectNode(img);
                selection.addRange(range);
            }

            document.execCommand('copy');
            setTimeout(() => { 
                img.remove();
                this.exit();
            }, 200);
        });
    }

    async save(): Promise<boolean> {
        let canvas = document.querySelector(`#${this.brushWrapperId} canvas`) as HTMLCanvasElement;
        if (!canvas) throw new Error('the "canvas" element is not exist.');

        if (typeof(this.drawOption.beforeSave) === 'function') {
            let bsvRst = await this.drawOption.beforeSave(canvas, this.exit);
            if (bsvRst === false) return false; //throw new Error('save is prevent.');
        }

        let link = document.createElement('a');
        let imageData = canvas.toDataURL('image/png', 1);

        let arr = imageData.split(','), mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while(n--) { u8arr[n] = bstr.charCodeAt(n); }
        let blobData = new Blob([u8arr], { type: mime });

        link.download = '画板' + new Date().getTime() + '.png';
        link.href = URL.createObjectURL(blobData);
        link.click();
        link.remove();
        link = null;

        if (typeof(this.drawOption.afterSave) === 'function') {
            let asvRst = await this.drawOption.afterSave(imageData);
            imageData = null;
            if (asvRst === false) return false; //throw new Error('exit is prevent.');
        }

        imageData = null;
        this.exit();
        return true;
    }

    async copy(): Promise<boolean> {
        let canvas = document.querySelector(`#${this.brushWrapperId} canvas`) as HTMLCanvasElement;
        if (!canvas) throw new Error('the "canvas" element is not exist.');

        if (typeof(this.drawOption.beforeCopy) === 'function') {
            let bcpRst = await this.drawOption.beforeCopy(canvas, this.exit);
            if (bcpRst === false) return false; //throw new Error('copy is prevent.');
        }

        let imageData = canvas.toDataURL('image/png', 1);
        let imageEl = document.createElement('img');
        imageEl.src = imageData;
        document.body.appendChild(imageEl);

        if (window.getSelection) {
            let selection = window.getSelection();
            selection.removeAllRanges();
            let range = document.createRange();
            range.selectNode(imageEl);
            selection.addRange(range);
        }

        document.execCommand('copy');
        imageEl.remove();
        imageEl = null;

        if (typeof(this.drawOption.afterCopy) === 'function') {
            let acpRst = await this.drawOption.afterCopy(imageData);
            imageData = null;
            if (acpRst === false) return false; //throw new Error('exit is prevent.');
        }

        imageData = null;
        this.exit();
        return true;
    }

    undo() {
        document.execCommand('undo');
    }

    redo() {
        document.execCommand('redo');
    }

    onKeydown(e): void {
        switch (e.keyCode) {
            case 27: this.exit(true); break;
            default: break;
        }
    }

    invoke(customDrawOption: IDrawOption = {}): Promise<any> {
        return this.startDraw(customDrawOption);
    }

    registerEventListener() {
        document.addEventListener('keydown', this.onKeydown);
    }

    unregisterEventListener() {
        document.removeEventListener('keydown', this.onKeydown);
    }

    // vue hook
    // ========================================================================================================

    created() {
        
    }

    beforeDestroy() {
        this.unregisterEventListener();
    }
}
