
import './index.scss';
import Screenshot from '../../Screenshot.vue';
import Sketchpad from '../../Sketchpad.vue';
import Vue from 'vue';

(function() {
    window.$screenshotInstance = null;
    window.$sketchpadInstance = null;
    const BdoScreenshotConstructor = Vue.extend(Screenshot);
    const BdoSketchpadConstructor = Vue.extend(Sketchpad);    

    const captureScreen = function(callback) {

        return new Promise((resolve, reject) => {
            let skpCloseCallback = null;
            window.$screenshotInstance.invoke({
                menus: [],
                beforeWorkspaceExit: () => {
                    if (typeof(skpCloseCallback) === 'function') {
                        skpCloseCallback();
                        skpCloseCallback = null;
                    }

                    if (typeof(callback) === 'function') {
                        callback();
                    }
                }
            }).then(result => {
                return window.$sketchpadInstance.invoke({
                    x: result.workspaceOption.x,
                    y: result.workspaceOption.y,
                    width: result.workspaceOption.width,
                    height: result.workspaceOption.height,
                    beforeCopy: (canvas, close) => {
                        result.addMask(canvas).then(success => {
                            close();
                            result.copy();

                        }).catch(err => {
                            console.log('beforeCopy', err);
                        });
                        return false;
                    },
                    beforeSave: (canvas, close) => {
                        result.addMask(canvas).then(success => {
                            close();
                            result.save();

                        }).catch(err => {
                            console.log('beforeCopy', err);
                        });
                        return false;
                    },
                    afterWorkspaceExit: () => {
                        result.close();
                    }
                });

            }).then(result => {
                skpCloseCallback = result.close;
                resolve(result);

            }).catch(err => {
                reject(err);
            });
        });
        
    }

    window.$bdosnap = {
        capture: function() {
            if (!window.$screenshotInstance || !window.$sketchpadInstance) {
                window.$screenshotInstance = new BdoScreenshotConstructor();
                window.$screenshotInstance.$mount();
                document.body.appendChild(window.$screenshotInstance.$el);
                window.$screenshotInstance.visible = true;
    
                window.$sketchpadInstance = new BdoSketchpadConstructor();
                window.$sketchpadInstance.$mount();
                document.body.appendChild(window.$sketchpadInstance.$el);
                window.$sketchpadInstance.visible = true;
            }

            let shotbtn = document.querySelector('#bdosnap');
            if (shotbtn) {
                shotbtn.style.display = 'none';
                // shotbtn.style.visibility = 'hidden';
            }

            captureScreen(() => {
                if (shotbtn) {
                    shotbtn.style.display = 'block';
                    // shotbtn.style.visibility = 'visible';
                }
            });
        },
        init: function(placement, stick, multipleIcons) {
            if (multipleIcons !== true) {
                if (window.parent.document.querySelector('#bdosnap')) return;
            }

            let iconDisplayPlacement = placement || 'left-bottom';  // ['left-bootom', 'right-bootom', 'left-top', 'right-top']    
            let stickEdge = stick === false ? false : true;
            let iconDisplayStyle = '';
            let shotbtn = document.createElement('div');
            let mousedownStartTime = 0;
            let mousedownEndTime = 0;
            let isClickEvent = false;
            

            switch (iconDisplayPlacement) {
                case 'left-bottom': iconDisplayStyle = 'left:0px;bottom:0px;'; break;
                case 'left-top': iconDisplayStyle = 'left:0px;top:0px;'; break;
                case 'right-bottom': iconDisplayStyle = 'right:0px;bottom:0px;'; break;
                case 'right-top': iconDisplayStyle = 'right:0px;top:0px;'; break;
                default: iconDisplayStyle = 'left:0px;bottom:0px;'; break;
            }            

            shotbtn.id = 'bdosnap';
            shotbtn.dataset.html2canvasIgnore = true;
            shotbtn.className = 'bdo-button';
            shotbtn.style.cssText = iconDisplayStyle;
            shotbtn.title = '1.点击进行截图\n2.按住左键进行位置拖动（支持窗口吸附）';

            if (stickEdge) {
                shotbtn.classList.add('fixed');
                shotbtn.classList.add(iconDisplayPlacement);
            }

            shotbtn.onclick = () => {
                if (mousedownEndTime - mousedownStartTime <= 200) {
                    isClickEvent = true;
                    shotbtn.style.display = 'none';
                    shotbtn.className = 'bdo-button';
                    window.$bdosnap.capture(() => {
                        shotbtn.style.display = 'block';
                    });

                } else {
                    isClickEvent = false;
                }
            };
            shotbtn.onmousedown = function (event) {
                shotbtn.className = 'bdo-button';
                isClickEvent = false;
                
                event = event || window.event;
                let bDrag = true;
                let disX = event.clientX - shotbtn.offsetLeft;
                let disY = event.clientY - shotbtn.offsetTop;
                let now = new Date().getTime();
                let iL = 0;
                let iT = 0;
                let maxL = document.documentElement.clientWidth - shotbtn.offsetWidth;
                let maxT = document.documentElement.clientHeight - shotbtn.offsetHeight;  
                
                mousedownStartTime = now;
                mousedownEndTime = now;
                this.setCapture && this.setCapture();
                    
                document.onmousemove = function (event2) {
                    if (!bDrag) return;
                    event2 = event2 || window.event;
                    iL = event2.clientX - disX;
                    iT = event2.clientY - disY;
                    iL = iL < 0 ? 0 : iL;
                    iL = iL > maxL ? maxL : iL;   
                    iT = iT < 0 ? 0 : iT;
                    iT = iT > maxT ? maxT : iT;
                    
                    shotbtn.style.marginTop = shotbtn.style.marginLeft = 0;
                    shotbtn.style.left = iL + 'px';
                    shotbtn.style.top = iT + 'px';
                    return false
                };                
    
    
                document.onmouseup = window.onblur = shotbtn.onlosecapture = function () {
                    bDrag = false;
                    shotbtn.releaseCapture && shotbtn.releaseCapture();
                    mousedownEndTime = new Date().getTime();
                    
                    if (!isClickEvent && mousedownEndTime - mousedownStartTime > 200) {                        
                        let classList = [];
                        if (iL <= 0) classList.push('left');
                        if (iL >= maxL) classList.push('right');
                        if (iT <= 0) classList.push('top');
                        if (iT >= maxT) classList.push('bottom');
                        if (classList.length > 0) {
                            shotbtn.classList.add('fixed');
                            shotbtn.classList.add(classList.join('-'));
                        }
                    }                    
                };
    
                return false;
            };

    
            let iconbtn = document.createElement('i');
            iconbtn.classList.add('bdo-icon-xiangji');
            iconbtn.classList.add('bdo-btnicon');            
            // iconbtn.style.cssText = 'font-size:24px;color:#606266;';
            // iconbtn.onmouseenter = e => { iconbtn.style.color = '#409EFF'; };
            // iconbtn.onmouseleave = e => { iconbtn.style.color = '#606266'; };

            
            
    
            shotbtn.appendChild(iconbtn);
            document.body.appendChild(shotbtn);
        },
        destroy: function() {
            // window.$screenshotInstance = null;
            // window.$sketchpadInstance = null;
            let captureIcon = document.querySelector('#bdosnap');
            if (captureIcon) captureIcon.remove();
        }
    };


    // window.addEventListener('load', () => {
    //     if (window.parent.document.querySelector('#bdosnap')) return;
    // });

    
})();


