













import { Component, Vue } from 'vue-property-decorator';
import html2canvas from 'html2canvas';

enum CaptureMode {
    VisibleRegion = 1,         // 窗口可见区
    FreeSelection = 2,         // 自由选区
    AssignedElement = 3,       // 页面指定元素
}

enum ExceptedError {
    CaptureCancel = 'cancel',
    CaptureAbort = 'abort',
    CaptureFinish = 'finish'
}

enum Menu {
    Close = 'close',
    Copy = 'copy',
    Save = 'save'
}

interface MenuItem {
    name: Menu;
    label: string;
    icon: string;
    action: (name: Menu) => void;
}

interface IWorkspaceOption {
    x: number;                    // 工作区实际使用区域偏移x
    y: number;                    // 工作区实际使用区域偏移y
    width: number;                // 工作区实际使用区域宽度
    height: number;               // 工作区实际使用区域高度
    maxAvailableWidth: number;    // 工作区最大可用宽度
    maxAvailableHeight: number;   // 工作区最大可用高度
}

interface ICaptureOption {
    x?: number;                    // 截屏开始坐标x
    y?: number;                    // 截屏开始坐标y
    width?: number;                // 截屏宽度
    height?: number;               // 截屏高度
    baseZIndex?: number;           // 纵向堆叠深度（默认1000）
    menus?: Menu[];                // 需要显示的菜单，默认全部
    filename?: string;             // 保存时的文件名
    captureMode?: CaptureMode;     // 截屏模式
    elementSelector?: string;      // 元素选择器 
    scaleCapture?: boolean;        // 是否允许缩放截取内容（仅当截取内容小于工作区域尺寸时有效，若截取内容大于工作实际使用区域尺寸，一律缩放至工作区域大小）
    exitByKeyEsc?: boolean;        // 是否允许键盘按键Esc退出

    // 截屏前回调函数，如果返回false，将会阻止截屏
    beforeCapture?: (captureMode: CaptureMode) => boolean | void;  

    // 截屏完成后的回调函数,如果返回false将取消将截屏内容添加到工作区
    afterCapture?: (width: number, height: number) => boolean | void;

    // 工作台加载完成后的回调函数（ws: wrokspace）
    onWorkspaceReady?: (wsX: number, wsY: number, wsWidth: number, wsHeight: number, baseZIndex: number) => void;

    // 退出工作台之前的回调函数，如果返回false，将阻止退出
    beforeWorkspaceExit?: () => boolean | Promise<boolean> | void;

    // 退出工作台之后的回调函数
    afterWorkspaceExit?: () => void;

    // 复制之前的回调函数，如果返回false，将阻止复制
    beforeCopy?: (canvas: HTMLCanvasElement) => boolean | Promise<boolean> | void;

    // 复制之后的回调函数，如果返回false，将阻止工作台退出 [imageData: base64格式的图片]
    afterCopy?: (imageData: string) => boolean | void;

    // 保存之前的回调函数，如果返回false，将阻止保存
    beforeSave?: (canvas: HTMLCanvasElement) => boolean | Promise<boolean> | void;

    // 保存之后的回调函数，如果返回false，将阻止工作台退出 [imageData: base64格式的图片]
    afterSave?: (imageData: string) => boolean | void;
}

interface ICaptureResult {
    message: string;
    workspaceOption: IWorkspaceOption;
    addMask: (canvas: HTMLCanvasElement) => Promise<boolean>;
    close: () => void;
    save: () => void;
    copy: () => void;
}

@Component({ name: 'Screenshot' })
export default class Screenshot extends Vue {

    private visible: boolean = false;
    private canvasId: string = '';
    private captureOption: ICaptureOption;
    private workspaceScale: number = 0.7;
    private workspaceOption: IWorkspaceOption;
    private workspaceId: string = 'screenshot_workspace';
    private canvasWrapperId: string = 'canvas_wrapper';
    private currentDocWinWidth: number;
    private currentDocWinHeight: number;

    private readonly captureCancel: ExceptedError = ExceptedError.CaptureCancel;
    private readonly captureAbort: ExceptedError = ExceptedError.CaptureAbort;
    private readonly captureFinish: ExceptedError = ExceptedError.CaptureFinish;

    private readonly defaultZIndex: number = 1000;

    private readonly menuOption: Record<Menu, MenuItem> = {
        [Menu.Close]: { name: Menu.Close, label: '取消', icon: 'bdo-icon-close', action: () => { this.exit(); } },
        [Menu.Save]: { name: Menu.Save, label: '保存', icon: 'bdo-icon-xiazai1', action: () => { this.save(); } },
        [Menu.Copy]: { name: Menu.Copy, label: '复制', icon: 'bdo-icon-seleted', action: () => { this.copy(); } }
    };

    private readonly defaultCaptureOption: ICaptureOption = {
        x: 0,
        y: window.scrollY,
        width: document.documentElement.clientWidth,
        height: document.documentElement.clientHeight,
        baseZIndex: this.defaultZIndex,
        menus: [Menu.Close, Menu.Save, Menu.Copy],
        captureMode: CaptureMode.VisibleRegion,
        elementSelector: 'html',
        scaleCapture: false,
        exitByKeyEsc: true
    };

    get menus(): MenuItem[] {
        let m: Menu[] = this.captureOption.menus || [];
        let mItems = m.map(menu => this.menuOption[menu] ? this.menuOption[menu] : null);
        return mItems.filter(item => item != null);
    }

    startCapture(customCaptureOption: ICaptureOption): Promise<any> {
        return new Promise((resolve, reject) => {
            this.registerEventListener();

            if (customCaptureOption && typeof(customCaptureOption.beforeCapture) === 'function') {
                let bcRst = customCaptureOption.beforeCapture(customCaptureOption.captureMode 
                    ? customCaptureOption.captureMode : this.defaultCaptureOption.captureMode);
                if (bcRst === false) throw new Error(this.captureCancel);
            }

            this.currentDocWinWidth = document.documentElement.clientWidth;
            this.currentDocWinHeight = document.documentElement.clientHeight;

            this.calculateOption(customCaptureOption).then(captureOption => {
                return this.createScreenshotCanvas(captureOption);

            }).then(canvas => {
                if (typeof(this.captureOption.afterCapture) === 'function') {
                    let acRst = customCaptureOption.afterCapture(this.captureOption.width, this.captureOption.height);
                    if (acRst === false) throw new Error(this.captureAbort);
                }
                return this.appendCanvasToWorkspace(canvas, this.captureOption);

            }).then(({ workspace, canvas }) => {
                return this.prepareCanvas(workspace, canvas as HTMLCanvasElement);

            }).then(workspaceOption => {
                if (typeof(this.captureOption.onWorkspaceReady) === 'function') {
                    let wrRst = this.captureOption.onWorkspaceReady(
                        workspaceOption.x,
                        workspaceOption.y,
                        workspaceOption.width,
                        workspaceOption.height,
                        this.captureOption.baseZIndex
                    );
                }
                throw new Error(this.captureFinish);

            }).catch((err: Error) => {
                const captureResult: ICaptureResult = {
                    message: err.message,
                    workspaceOption: this.workspaceOption,
                    addMask: canvas => { return this.addMask(canvas); },
                    close: () => { this.exit(); },
                    save: () => { this.save(); },
                    copy: () => { this.copy(); }
                };

                switch (err.message) {
                    case ExceptedError.CaptureCancel:
                    case ExceptedError.CaptureAbort:
                    case ExceptedError.CaptureFinish:
                        resolve(captureResult);
                        break;

                    default: reject(err); break;
                }
            });
        });
    }

    async calculateOption(customCaptureOption: ICaptureOption): Promise<ICaptureOption> {
        let currentOption = Object.assign({}, this.defaultCaptureOption, customCaptureOption || {});
        switch (currentOption.captureMode) {
            case CaptureMode.VisibleRegion:
                currentOption.x = 0;
                currentOption.y = window.scrollY;
                currentOption.width = this.currentDocWinWidth;
                currentOption.height = this.currentDocWinHeight;
                // currentOption.elementSelector = 'html';
                this.captureOption = currentOption;
                return this.captureOption;

            case CaptureMode.FreeSelection:
                currentOption.elementSelector = 'html';

                this.captureOption = currentOption;
                return this.captureOption;

            case CaptureMode.AssignedElement:
                let x = 0, y = 0, w = this.currentDocWinWidth, d = this.currentDocWinHeight;
                if (customCaptureOption.elementSelector) {
                    let el = document.querySelector(customCaptureOption.elementSelector);
                    if (el) {
                        let rect = el.getBoundingClientRect();
                        x = rect.left;
                        y = rect.top;
                        w = el.clientWidth;
                        d = el.clientHeight;
                    }
                }
                currentOption.x = x;
                currentOption.y = y;
                currentOption.width = w;
                currentOption.height = d;
                this.captureOption = currentOption;
                return this.captureOption;

            default:
                this.captureOption = currentOption;
                return this.captureOption;
        }
    }

    checkElInWindowVisibleRange(el): boolean {
        // let top = 0, left = 0;

        // while(el) {
        //     top += el.offsetTop;
        //     left += el.offsetLeft;

        //     el = el.offsetParent;
        // }

        let rect = el.getBoundingClientRect();
        return (
            rect.top >= 0 && rect.left >= 0 &&
            rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
            rect.right <= (window.innerWidth || document.documentElement.clientWidth)
        );

        // return Boolean(el.offsetTop + el.clientHeight > window.pageYOffset && window.pageYOffset + window.innerHeight > el.offsetTop);
    }

    async createScreenshotCanvas(captureOption: ICaptureOption): Promise<HTMLCanvasElement> {
        return html2canvas(document.querySelector(captureOption.elementSelector), {
            x: captureOption.x,
            y: captureOption.y,
            width: captureOption.width,
            height: captureOption.height,
            logging: true, 
            allowTaint: true,
            useCORS: true,
            // ignoreElements: el => {
            //     return false;
            // }
        });
    }

    calcScaleRatio(trgW, trgH, srcW, srcH, scalable): number {
        let offsetX = srcW - trgW,
            offsetY = srcH - trgH;

        switch (true) {
            case offsetX > 0 && offsetY < 0: return trgW / srcW;
            case offsetX < 0 && offsetY > 0: return trgH / srcH;
            case offsetX > 0 && offsetY > 0: return offsetX > offsetY ? trgW / srcW : trgH / srcH;
            case offsetX < 0 && offsetY < 0: return offsetX > offsetY ? trgH / srcH : trgW / srcW;
            default: return 1;
        }
    }

    calcWorkspaceOption(captureOption: ICaptureOption): IWorkspaceOption {
        let maxWidth = this.currentDocWinWidth * this.workspaceScale,
            maxHeight = this.currentDocWinHeight * this.workspaceScale;

        let scaleRatio = this.calcScaleRatio(maxWidth, maxHeight, 
            captureOption.width, captureOption.height, captureOption.scaleCapture);

        let actualWidth = captureOption.width * scaleRatio,
            actualHeight = captureOption.height * scaleRatio;

        this.workspaceOption = {
            x: (this.currentDocWinWidth - actualWidth) / 2,
            y: (this.currentDocWinHeight - actualHeight) / 2,
            width: actualWidth,
            height: actualHeight,
            maxAvailableWidth: this.currentDocWinWidth,
            maxAvailableHeight: this.currentDocWinHeight
        };

        return this.workspaceOption;
    }

    async appendCanvasToWorkspace(canvas: HTMLCanvasElement, captureOption: ICaptureOption): Promise<Record<string, HTMLElement>> {
        let workspaceOption = this.calcWorkspaceOption(captureOption);
    
        let workspace = document.querySelector(`#${this.workspaceId}`) as HTMLElement;
        if (!workspace) throw new Error('can not find this "workspce" element.');

        let canvasWrapper = workspace.querySelector(`#${this.canvasWrapperId}`) as HTMLElement;
        if (!canvasWrapper) throw new Error('can not find this "canvasWrapper" element.');

        this.visible = true;
        this.canvasId = 'screenshot_' + new Date().getTime();
        canvas.id = this.canvasId;
        canvas.style.zIndex = (this.captureOption.baseZIndex + 2).toString();
        canvas.style.transition = 'all 0.3s cubic-bezier(0.42, 0, 0.58, 1) 0s';
        canvasWrapper.innerHTML = '';
        canvasWrapper.appendChild(canvas);

        return { workspace, canvas };
    }
    
    prepareCanvas(workspace: HTMLElement, canvas: HTMLCanvasElement): Promise<IWorkspaceOption> {
        if (!canvas || !workspace) throw new Error('can not prepare canvas, because the "canvas" or "workspace" element is not exist.');
        
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                this.addBlurEffectToBackground();

                workspace.style.backgroundColor = 'rgba(0, 0, 0, 0.4)';
                canvas.style.position = 'absolute';
                canvas.style.top = `${this.workspaceOption.y}px`;
                canvas.style.left = `${this.workspaceOption.x}px`;
                canvas.style.width = `${this.workspaceOption.width}px`;
                canvas.style.height = `${this.workspaceOption.height}px`;

                resolve(this.workspaceOption);
            }, 200);
        });
    }

    addBlurEffectToBackground(): void {
        let others = document.querySelectorAll('body > *:not([data-html2canvas-ignore=true])'); 
        if (!others) return;
        others.forEach((el: HTMLElement) => {
            if(el.nodeName != 'SCRIPT') el.style.webkitFilter = 'blur(8px)';
        });
    }

    removeBlurEffectToBackground(): void {
        let others = document.querySelectorAll('body > *:not([data-html2canvas-ignore=true])'); 
        if (!others) return;
        others.forEach((el: HTMLElement) => {
            if(el.nodeName != 'SCRIPT') el.style.webkitFilter = ''; 
        });
    }

    mergeCanvas(from, to): Promise<boolean> {
        return new Promise((resolve, reject) => {
            if (!from || !to) reject(false);

            let sourceImageData = from.toDataURL('image/png');
            let destCanvasContext = to.getContext('2d');

            let destImage = new Image;
            destImage.onload = () => {
                destCanvasContext.drawImage(destImage, 0, 0, from.width, from.height, 0, 0, to.width, to.height);
                resolve(true);
            };
            destImage.onerror = () => {
                reject(false);
            };
            destImage.src = sourceImageData;
        });
    }

    addMask(canvas: HTMLCanvasElement): Promise<boolean> {
        let canvasTrg = document.querySelector(`#${this.canvasId}`) as HTMLCanvasElement;
        if (!canvasTrg) return Promise.reject(false);

        return this.mergeCanvas(canvas, canvasTrg);
    }

    async exit(keyEsc?: boolean): Promise<boolean> {
        if (keyEsc === true && !this.captureOption.exitByKeyEsc) throw new Error('esc is prevent.');

        if (typeof(this.captureOption.beforeWorkspaceExit) === 'function') {
            let bweRst = await this.captureOption.beforeWorkspaceExit();
            if (bweRst === false) throw new Error('exit is prevent.');
        }

        let workspace = document.querySelector(`#${this.workspaceId}`) as HTMLElement;
        if (!workspace) throw new Error('the "workspace" element is not exist.');

        let canvasWrapper = workspace.querySelector(`#${this.canvasWrapperId}`) as HTMLElement;
        canvasWrapper.style.transform = 'scale(0)';
        workspace.style.backgroundColor = '';
        
        this.removeBlurEffectToBackground();

        setTimeout(() => {
            this.visible = false;
            canvasWrapper.innerHTML = '';
            canvasWrapper.style.transform = 'scale(1)';

            if (typeof(this.captureOption.afterWorkspaceExit) === 'function') {
                this.captureOption.afterWorkspaceExit();
            }
        }, 500);

        this.unregisterEventListener();
        
        return true;
    }

    async save(): Promise<boolean> {
        let canvas = document.querySelector(`#${this.canvasId}`) as HTMLCanvasElement;
        if (!canvas) throw new Error('the "canvas" element is not exist.');

        if (typeof(this.captureOption.beforeSave) === 'function') {
            let bsvRst = await this.captureOption.beforeSave(canvas);
            if (bsvRst === false) return false; //throw new Error('save is prevent.');
        }

        let link = document.createElement('a');
        let imageData = canvas.toDataURL('image/png', 1);

        let arr = imageData.split(','), mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while(n--) { u8arr[n] = bstr.charCodeAt(n); }
        let blobData = new Blob([u8arr], { type: mime });

        link.download = '屏幕截图' + new Date().getTime() + '.png';
        link.href = URL.createObjectURL(blobData);
        link.click();
        link.remove();
        link = null;

        if (typeof(this.captureOption.afterSave) === 'function') {
            let asvRst = await this.captureOption.afterSave(imageData);
            imageData = null;
            if (asvRst === false) return false; // throw new Error('exit is prevent.');
        }

        imageData = null;
        this.exit();
        return true;
    }

    async copy(): Promise<boolean> {
        let canvas = document.querySelector(`#${this.canvasId}`) as HTMLCanvasElement;
        if (!canvas) throw new Error('the "canvas" element is not exist.');

        if (typeof(this.captureOption.beforeCopy) === 'function') {
            let bcpRst = await this.captureOption.beforeCopy(canvas);
            if (bcpRst === false) throw new Error('copy is prevent.');
        }

        let imageData = canvas.toDataURL('image/png', 1);
        let imageEl = document.createElement('img');
        imageEl.src = imageData;
        document.body.appendChild(imageEl);

        if (window.getSelection) {
            let selection = window.getSelection();
            selection.removeAllRanges();
            let range = document.createRange();
            range.selectNode(imageEl);
            selection.addRange(range);
        }

        document.execCommand('copy');
        imageEl.remove();
        imageEl = null;

        if (typeof(this.captureOption.afterCopy) === 'function') {
            let acpRst = await this.captureOption.afterCopy(imageData);
            imageData = null;
            if (acpRst === false) throw new Error('exit is prevent.');
        }

        imageData = null;
        this.exit();
        return true;
    }

    onKeydown(e): void {
        switch (e.keyCode) {
            case 27: this.exit(true); break;
            default: break;
        }
    }


    invoke(customCaptureOption: ICaptureOption = {}): Promise<any> {
        return this.startCapture(customCaptureOption);
    }

    registerEventListener() {
        document.addEventListener('keydown', this.onKeydown);
    }

    unregisterEventListener() {
        document.removeEventListener('keydown', this.onKeydown);
    }
    

    // vue hook
    // ========================================================================================================

    created() {

    }

    beforeDestroy() {
        this.unregisterEventListener();
    }
}
